/*http://zellwk.com/blog/masonry-with-susy/*/
.masonry {
@include span($mobilecol);
       
        @include clearfix;
        @include breakpoint ($tablet){
            @include span(8 of $tabletcol);
            
        }
        @include breakpoint ($desktop){
           @include span(12 of $desktopcol);
            @include gutters(.3em);/*Toujours en dernier pour override de la taille des gouttieres*/
        }
        //Pour orientation paysage
         @include breakpoint ($tabletlandscape){
           @include span(3 of $tabletcol-landcsape);
            
        }
}
.masonry--gutter {
  width: gutter();
}
.masonry--column {
  width: span(3); 
}
.masonry--item {
    @include gallery(3 of $mobilecol);
       
        @include clearfix;
        @include breakpoint ($tablet){
            @include gallery(3 of $tabletcol);
            
        }
        @include breakpoint ($desktop){
   @include gallery(3 of $desktopcol);
        }
        //Pour orientation paysage
         @include breakpoint ($tabletlandscape){
        @include gallery(3 of $tabletcol-landcsape);
            
        }
   
  //width: span(3);
  margin-top: gutter();
}

